<template>
    <div class="container banner__wrapper">
        <div class="banner__gradient"
             :class="bannerColor"
        >
            <span class="banner__title">
                Для пользователей тарифа «{{tariff}}»
            </span>
        </div>
        <div class="banner__description_wrapper">
            <img src="../assets/images/domofon.svg"
                 alt="Домофон"
                 class="banner__description_img"
            />
            <p class="banner__description_text">
                Только для вас подписка на услугу «Умный домофон» доступна абсолютно <span>бесплатно</span>
            </p>
        </div>
    </div>
</template>

<script>
    import { defineComponent } from "vue";
    import { getAllServices } from "./ApiRequests";

    export default defineComponent({
        name: "VipBanner",
        data() {
            return {
                tariff: String,
                bannerColor: String
            };
        },
        watch: {
            tariff() {
                this.checkBannerColor()
            },
        },
        async mounted() {
            await this.getAllUserServices();
        },
        methods: {
            async getAllUserServices() {
                let allServices = await getAllServices();
                this.tariff = allServices.find(service => service["TYPE"] === "UNLIM").NAME
            },
            checkBannerColor() {
                switch (this.tariff) {
                    case "Восход":
                        this.bannerColor = "blue"
                        break
                    case "Взлет":
                        this.bannerColor = "green"
                        break
                    case "Сияние":
                        this.bannerColor = "red"
                        break
                    default:
                        this.bannerColor = "purple"
                }
            }
        }
    });
</script>

<style scoped>
.banner__gradient {
  width: 100%;
  display: flex;
  margin-top: 24px;
  padding: 6px 0 32px;
  border-radius: 14px;
}

.purple {
  background: linear-gradient(90deg, #1274FF 0%, #8A4CF0 100%);
}

.green {
  background: linear-gradient(90deg, #09C93F 0%, #00C4B8 100%);
}

.red {
  background: linear-gradient(90deg, #FCC504 0%, #F046A2 120.55%);
}

.blue {
  background: linear-gradient(90deg, #2FDAFF 0%, #7263BF 100%);;
}

.banner__title {
  width: 100%;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: #FFFFFF;
  border-radius: 14px;
}

.banner__description_wrapper {
  display: flex;
  padding: 15px 16px;
  margin-top: -26px;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  background: #FFFFFF;
  border-radius: 14px;
  z-index: 1;
  filter: drop-shadow(0px 2px 10px rgba(6, 16, 41, 0.1));
}

.banner__description_img {
  width: 34px;
  height: 34px;
  margin-right: 20px;
}

.banner__description_text span {
  color: #1274FF;
}
</style>